@media (min-width: 480px) and (max-width: 769px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 1fr;
    gap: 0;
    height: 100%;
  }
  .hero__conteiner--left, .hero__conteiner--right {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    width: 100vw;
    background-image: image-set(url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg"));
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95%;
  }
  .hero__conteiner--right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
  .hero__conteiner--title {
    order: 2;
    height: auto;
    margin-bottom: 0;
  }
  .hero__conteiner--title h1 {
    font-size: 7vw;
  }
  .hero__conteiner--paragraph {
    text-align: center;
    width: 98vw;
    height: auto;
    order: 3;
  }
  .hero__conteiner--paragraph p {
    font-size: 3vw;
    line-height: 6vh;
  }
  .hero__conteiner--btn {
    order: 1;
    width: 70vw;
    font-size: 4vw;
  }
}